<template>
    <div class="row">
        <!-- 菜单 -->
        <div class="col-sm-12 col-md-9">
            <!-- <table class="table">
                <thead class="thead-default">
                    <tr>
                        <th>名称</th>
                        <th>价格</th>
                        <th>加入</th>
                    </tr>
                </thead>
                <tbody v-for="item in getMenuItems" :key="item.name">
                    <tr>
                        <td>
                            <strong>{{item.name}}</strong>
                        </td>
                        <td>{{item.price}}</td>
                        <td><button @click="addToBasket(item)" class="btn btn-sm btn-outline-success">+</button></td>
                    </tr>                    
                </tbody>
            </table> -->
            <div class="list-group text-center">
                <button type="button" class="list-group-item list-group-item-light">请选择您要食用的类型并加入购物车</button>
                <button type="button" class="list-group-item list-group-item-action list-group-item-warning" @click="showMenu('主食')">主食</button>
                <button type="button" class="list-group-item list-group-item-action list-group-item-info" @click="showMenu('配餐')">配餐</button>
                <button type="button" class="list-group-item list-group-item-action list-group-item-success" @click="showMenu('饮料')">饮料</button>
                <button type="button" class="list-group-item list-group-item-action list-group-item-danger" @click="showMenu('甜品')">甜品</button>
            </div>
            <div class="row">
                <div class="col-md-3 space" v-for="item in result" :key="item.name">
                    <div class="card">
                        <img class="card-img-top" :src="item.picture" alt="Card image cap">
                        <div class="card-body">
                            <h5 class="card-title text-center">{{item.name}}</h5>
                            <p class="card-text"><small class="text-muted">{{item.description}}</small></p>
                            <span class="">{{item.price}}¥/份</span>
                            <button @click="addToBasket(item)" class="btn btn-sm btn-outline-success float-right">加入购物车+</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- 购物车 -->
        <div class="col-sm-12 col-md-3">
            <div v-if="baskets.length>0">
            <table class="table">
                <thead class="thead-default">
                    <tr>
                        <th>数量</th>
                        <th>名称</th>
                        <th>价格</th>
                    </tr>
                </thead>
                <tbody v-for="item in baskets" :key="item.name">
                    <tr>
                        <td>
                            <button @click="decreaseQuantity(item)" class="btn btn-sm">-</button>
                            <span>{{item.quantity}}</span>
                            <button @click="increaseQuantity(item)" class="btn btn-sm">+</button>
                        </td>
                        <td>{{item.name}}</td>
                        <td>{{item.price*item.quantity}}</td>
                    </tr>
                </tbody>
            </table>
            <p>总价：{{total+"RMB"}}</p>
            <button class="btn btn-success btn-block">支付</button>
            </div>
            <div v-else>
                {{basketText}}
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            baskets:[],
            basketText:"购物车没有任何商品",
            // getMenuItems:{}
            result:[]
        }
    },
    created(){
        // this.http.get("menu.json").then(res=>this.getMenuItems=res.data)
        this.http.get("wxbmenu.json").then(res=>this.$store.commit("setMenuItems",res.data))
    },
    methods:{
        addToBasket(item){
            let basket=({
                name:item.name,
                price:item.price,
                quantity:1
            })
            if(this.baskets.length>0){
                let result=this.baskets.filter((basket)=>{
                    return (basket.name===item.name&&basket.price===item.price)
                })
                if(result!=null&&result.length>0){
                    result[0].quantity++
                }
                else{
                    this.baskets.push(basket)
                }
            }
            else{
                this.baskets.push(basket)
            }
        },
        decreaseQuantity(item){
            item.quantity--
            if(item.quantity<=0){
                this.removeFromBasket(item)
            }
        },
        increaseQuantity(item){
            item.quantity++
        },
        removeFromBasket(item){
            this.baskets.splice(this.baskets.indexOf(item),1)
        },
        showMenu(type){
            // this.http.get('/menu.json').then(res=>{
            //     const data=res.data
            //     const menus=[]
            //     for(let key in data){
            //         const menu=data[key]
            //         menus.push(menu)
            //     }
            //     //实现过滤
            //     this.result=menus.filter((menu)=>{
            //         return menu.type=="饮料"
            //     })
            // })   
            const data=this.$store.getters.getMenuItems
            const menus=[]
            for(let key in data){
                const menu=data[key]
                menus.push(menu)
            }
            //实现过滤
            this.result=menus.filter((menu)=>{
                return menu.type==type
            })
        },
    },
    computed:{
        total(){
            let totalCost=0
            for(let index in this.baskets){
                let individualItem=this.baskets[index]
                totalCost+=individualItem.quantity*individualItem.price
            }
            return totalCost
        },
        getMenuItems(){
            // return this.$store.state.menuItems
            //通过getters获取数据
            return this.$store.getters.getMenuItems
        }
    }
}
</script>

<style scoped>
.space{
    padding-bottom:15px;
}
</style>



