<template>
    <form @submit.prevent="addMenuItem">
        <h3 class="text-muted my-5 text-center">添加新的商品</h3>
        <div class="form-group row">
            <label class="col-sm-1">名称</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newCommodity.name" required>
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">类别</label>
            <div class="col-sm-11">
            <select class="form-control" v-model="newCommodity.type" required>
            <option>主食</option>
            <option>配餐</option>
            <option>饮料</option>
            <option>甜品</option>
            </select>
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">价格</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newCommodity.price" required>
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">描述</label>
            <div class="col-sm-11">
                <textarea rows="5" class="form-control" v-model="newCommodity.description" required></textarea>
            </div>
        </div>      
        <div class="form-group row">
            <label class="col-sm-1">图片</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newCommodity.picture" required>
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1"></label>
            <div class="col-sm-11">
            <button type="submit" class="btn btn-success btn-block">添加</button>
            </div>
        </div>
    </form>
</template>

<script>
export default {
    data(){
        return{
            newCommodity:{
                picture:"/static/img/default.jpg"
            },
        }
    },
    methods:{
        addMenuItem(){
            let data={
                name:this.newCommodity.name,
                type:this.newCommodity.type,
                description:this.newCommodity.description,
                price:this.newCommodity.price,
                picture:this.newCommodity.picture
            }
            // fetch("https://first-vue-project-2793d.firebaseio.com/menu.json",{
            //     method:"POST",
            //     headers:{
            //         "Content-type":'application/json'
            //     },
            //     body:JSON.stringify(data)
            // })
            // .then(res=>res.json())
            // .then(this.$router.push({name:"menuLink"}))
            // .catch(err=>console.log(err))
            // this.http.post("menu.json",data).then(res=>this.$router.push({name:"menuLink"}))
            this.http.post("wxbmenu.json",data).then(res=>this.$store.commit("pushToMenuItems",data))
        }
    }
}
</script>




