<template>
    <div class="row">   
        <div class="col-sm-12 col-md-6">
            <!-- new pizza -->
            <app-new-menu></app-new-menu>
        </div>    
        <div class="col-sm-12 col-md-6">
            <h3 class="text-muted my-5 text-center">菜单管理</h3>
            <table class="table">
                <thead class="table table-default">
                    <tr>
                        <th>名称</th>
                        <th>类别</th>
                        <th>价格</th>
                        <th>删除</th>
                    </tr>
                </thead>
                <tbody v-for="item in getMenuItems" :key="item.name">
                    <tr>
                        <td>{{item.name}}</td>
                        <td>{{item.type}}</td>
                        <td>{{item.price}}</td>
                        <td><button @click="deleteData(item)" class="btn btn-outline-danaer btn-sm">&times;</button></td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import NewMenu from './NewMenu'

export default {
    data(){
        return{
            //getMenuItems:[]
        }
    },
    created(){
        this.http.get("wxbmenu.json").then(res=>this.$store.commit("setMenuItems",res.data))
    },
    updated(){
        // fetch("https://first-vue-project-2793d.firebaseio.com/menu.json")
        // .then(res=>{return res.json()})
        // .then(
        //     data=>{
        //         let menuArray=[]
        //         for(let key in data){
        //             data[key].id=key
        //             menuArray.push(data[key])
        //         }
        //         // this.getMenuItems=menuArray
        //         this.$store.commit('setMenuItems',menuArray)
        //     }
        // )
        this.http.get('wxbmenu.json')
        .then(res=>res.data)
        .then(
            data=>{
                let menuArray=[]
                for(let key in data){
                    data[key].id=key
                    menuArray.push(data[key])
                }
                // this.getMenuItems=menuArray
                this.$store.commit('setMenuItems',menuArray)
            }
        )
    },
    beforeRouteEnter(to,from,next){
        next(vm=>{if(vm.$store.getters.isLogin===false){
            alert("该页面需要登录才能访问，请先登录！")
            next({name:"loginLink"})
        }})
    },
    methods:{
        deleteData(item){
            // fetch("https://first-vue-project-2793d.firebaseio.com/menu/"+item.id+"/.json",{
            //     method:"DELETE",
            //     headers:{
            //         'Content-type':'application/json'
            //     }
            // })
            // .then(res=>res.json())
            // // .then(data=>this.$router.push({name:"menuLink"}))
            // .then(data=>{this.$store.commit('removeMenuItems',item)})
            // .catch(err=>console.log(err))
            this.http.delete("wxbmenu/"+item.id+"/.json")
            .then(res=>res.data)
            // .then(data=>this.$router.push({name:"menuLink"}))
            .then(data=>{this.$store.commit('removeMenuItems',item)})
            .catch(err=>console.log(err))
        }
    },
    computed:{
        getMenuItems:{
            //在vuex中获取数据
            get(){
                // return this.$store.state.menuItems
                 //通过getters获取数据
                return this.$store.getters.getMenuItems
            },
            set(){}
        }
    },
    components:{
        appNewMenu:NewMenu
    }
}
</script>



